<template>
    <div class="container">
		<div class="row">
			<div class="col-md-3 col-ms-6 bar" style="margin-top: 6.125rem;">
				<h5><img src="../assets/contact/chat.png" />13156248060</h5>
				<h5><img src="../assets/contact/weixin.png" />Fangcaojushi</h5>
				<h5><img src="../assets/contact/QQ.png" />59990372</h5>
				<h5><img src="../assets/contact/mail.png" />fangcaojushi@163.com</h5>
			</div>
			<div class="col-md-6 col-ms-12">
				<h1 class="load text1">
					你好，    
				</h1>
				<h1 class="load text2">
					我是芳草居，
				</h1>
				<h1 class="load text3">
					前端开发者。
				</h1>
			</div>
			
			<div class="col-md-3 col-ms-6 bar">
				<h6>精通web前端的开发，软件及手机界面开发</h6>
				<h6>
					精通Html5、Bootstrap、css3、javascript、jquery、ajax、json、xml
				</h6>
				<h6>
					熟练Echarts、Highcharts 大数据的统计图表的开发
				</h6>
				<h6>
					熟练Bmap、mapv、geo、mapbox地图的开发
				</h6>
				<h6>
					熟悉Vue.js、node.js 、npm、webpack、git
				</h6>
				<h6>
					熟悉asp.net和SQL Server数据库 网站开发、移动端H5的设计，flash动画设计
				</h6>
			</div>
		</div>
