</template>

<script>
    export default {
        name: "Home"
    }
</script>

<style scoped>
h1{
	font-size: 5rem;
	text-align: left;
	
}
li{
	list-style: -moz-amharic ;
	line-height: 2.125rem;
	font-size: 1.25rem;
}
img{
	width: 1.6875rem;
	height: 1.875rem;
	margin: 0 0.9375rem;
}
h5{
}
.bar{
	background: #f7f7f7;
	text-align: left;
	cursor: default;
	-moz-transition: background 0.3s ease-in-out;
	-webkit-transition: background 0.3s ease-in-out;
	transition: background 0.3s ease-in-out;
	padding: 1.25rem;
}
.bar:hover {
   color: #ffffff;
   background: #56c553;
}
/*光标动画*/
@keyframes caret {
	100% {
	border-color:transparent;
}
}/*文本动画：从宽度为0开始*/
@keyframes text {
	from {
	width:0;
}
}
.load {
	width:20ch;
	margin:0 auto;
	overflow:hidden;
	word-break:keep-all;
}
.text1 {
	animation: caret .5s infinite step-end,text 1s steps(11);
}
.text2 {
	animation:caret 25s infinite step-end, text 2s steps(11);
}
.text3 {
	animation:caret 55s infinite step-end, text 5s steps(11);
}
</style>



